.rjcv-toast {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 2000;
  padding: 0.3rem;
  font-size: 0.3rem;
  line-height: 1.2;
  color: #fff;
  text-align: center;
  word-break: break-all;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 0.2rem;
  transform: translate3d(-50%, -50%, 0); }

.rjcv-fade-enter-active, .rjcv-fade-leave-active {
  transition: opacity .3s ease; }

.rjcv-fade-enter, .rjcv-fade-leave-to {
  opacity: 0 !important; }

